import React, { useState, useEffect } from "react";
import ErrorMessage from "./ErrorMessage";
import TableForm from "./TableForm";
import Papa from "papaparse";
import { Button } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";

function InputForm() {
  const [csvfile, setCsvfile] = useState("");
  const [fileData, setFileData] = useState("");
  const [fileError, setFileError] = useState(false);

  // –ü—Ä–æ–≤–µ—Ä–∫–∞ —Ñ–∞–π–ª–∞ –Ω–∞ –ø—Ä–∞–≤–∏–ª—å–Ω–æ—Å—Ç–∏ —Ç–∏–ø–∞
  useEffect(() => {
    if (csvfile.type !== "text/csv") {
      setFileError(true);
    }
    if (csvfile.type === "text/csv" || csvfile.length === 0) {
      setFileError(false);
    }
  }, [csvfile]);

  const handleChange = (event) => setCsvfile(event.target.files[0]);

  // –ü–æ–ª—É—á–∞–µ–º –¥–∞–Ω–Ω—ã–µ –∏–∑ —Ñ–∞–π–ª–∞
  const importCSV = () => {
    Papa.parse(csvfile, { complete: setFileData, header: true });
  };

  return (
    <>
      <div>
        <h2>–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –¥–æ–±–∞–≤—Ç–µ –í–∞—à —Ñ–∞–π–ª!</h2>
        <input type="file" name="file" onChange={handleChange} />
        <p />
        {csvfile.length !== 0 && (
          <Button onClick={importCSV} disabled={fileError}>
            –ó–∞–≥—Ä—É–∑–∏—Ç—å –¥–∞–Ω–Ω—ã–µ!
          </Button>
        )}
        <p />
      </div>
      {fileError && (
        <ErrorMessage error="–í—ã–±—Ä–∞–Ω –Ω–µ –ø—Ä–∞–≤–∏–ª—å–Ω—ã–π —Ñ–æ—Ä–º–∞—Ç —Ñ–∞–π–ª–∞. –ü–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç—Å—è —Ç–æ–ª—å–∫–æ *.csv —Ñ–∞–π–ª—ã." />
      )}
      {fileData && <TableForm dataTable={fileData} />}
    </>
  );
}

export default InputForm;
