import React from "react";
import { Table } from "react-bootstrap";
import ErrorMessage from "./ErrorMessage";
import { greatRow, checkHeader } from "./helpers";

function TableForm({ dataTable }) {
  let headerData = dataTable.data.map((el) => Object.keys(el))[0];
  return dataTable.data.length !== 0 ? (
    checkHeader(dataTable.data) ? (
      <Table striped bordered hover size="sm">
        <thead>
          <tr>
            <th>ID</th>
            {headerData.map((data, inx) => (
              <th key={inx}>{data.trim()}</th>
            ))}
            <th>Duplicate with</th>
          </tr>
        </thead>
        <tbody>
          {dataTable.data.map((data, index) => {
            return (
              Object.keys(data).length > 3 &&
              greatRow(dataTable.data, data, index)
            );
          })}
        </tbody>
      </Table>
    ) : (
      <ErrorMessage error="–í–Ω—É—Ç—Ä–∏ —Ñ–∞–π–ª–∞ –æ–±–Ω–∞—Ä—É–∂–µ–Ω–∞ –Ω–µ –ø—Ä–∞–≤–∏–ª—å–Ω–∞—è —Å—Ç—Ä—É–∫—Ç—É—Ä–∞ –∫–æ–ª–æ–Ω–æ–∫. –ü–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç—Å—è —Ç–æ–ª—å–∫–æ –∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–µ *.csv —Ñ–∞–π–ª—ã." />
    )
  ) : (
    <ErrorMessage error="–í—ã–±—Ä–∞–Ω —Ñ–∞–π–ª–∞ —Å –Ω–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ–π —Å—Ç—Ä—É–∫—Ç—É—Ä–æ–π —Ñ–∞–π–ª–∞. –ü–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç—Å—è —Ç–æ–ª—å–∫–æ –∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–µ *.csv —Ñ–∞–π–ª—ã." />
  );
}

export default TableForm;
